.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


html{
  background-color: #000000;
  margin-top: 50px;
  filter: brightness(1.11) saturate(1.11); /* Adjust the value as needed */
  overflow-x: hidden;
}


.headerImage {
  width: 60px;
}

.App-header {
  background-color: #000000;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
 align-items: center;
  
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow-x: hidden;
  
}

/* Centered Header Image */
.headerImageContainer {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}


.App-link {
  color: #61dafb;
}


.image-container {
  display: flex;
  overflow: hidden;
  white-space: nowrap; /* Prevent images from wrapping onto a new line */
  
}


.image-row {
  
  display: flex;
  flex: nowrap;
  animation: scroll-left 70s linear infinite; /* Adjust the duration for scrolling speed */
}

.image-row-mobile {
  
  display: flex;
  flex: nowrap;
  animation: scroll-left 70s linear infinite; /* Adjust the duration for scrolling speed */
}

.image-row-logo {
    /* Adjust the image width based on your design */ 

  display: flex;
  flex: nowrap;
  animation: scroll-left 70s linear infinite; /* Adjust the duration for scrolling speed */
  
}



.image-row img:hover{
  filter: brightness(1.15) contrast(1);
  transition: transform .75s ease-in-out, filter .75s ease-in-out;
  
}

.image-row-mobile img:hover{
  filter: brightness(1.1) contrast(1);
  transition: transform .75s ease-in-out, filter .75s ease-in-out;
  
}

.image-row-logo img:hover{
  transition: transform .75s ease-in-out, filter .75s ease-in-out;
  
  
}

.image-row img {
  margin-top:40px;
  margin-bottom:40px;
  transition: transform .75s ease-in-out, filter .75s ease-in-out;
  width: 380px; /* Adjust the image width based on your design */
  height: auto;
  margin-right: 40px; /* Adjust space between images */
  cursor: pointer;
  object-fit: contain;
  
  
}

.image-row-mobile img {
  margin-top:40px;
  margin-bottom:40px;
  transition: transform .75s ease-in-out, filter .75s ease-in-out;
  width: 150px; /* Adjust the image width based on your design */
  height: auto;
  margin-right: 90px; /* Adjust space between images */
  cursor: pointer;
  object-fit: contain;

}

.image-row-logo img {
  width: 100px;
  
  object-fit: contain;
  margin-top:20px;
  margin-bottom:30px;
  cursor: pointer;
  
 
  margin-right: 150px;
  
}

@keyframes scroll-left {
  0% {
    transform: translateX(0%); /* Start at the original position */
  }
  100% {
    transform: translateX(-40%); /* Move to the left by 50% of the container's width */
  }
}

.tilt-image {
  width: 100px; /* Adjust as needed */
  height: auto;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.tilt-image-container {
  flex: 0 0 auto; /* Keeps each image at a fixed width */
}


/* Left-Aligned Section Titles */
.categoryTitle {
  text-align:center;
  font-size: .75em;
  
}



::selection {
  background-color: rgb(255, 255, 255); /* Choose any shade of green */
  color: black; /* Adjust text color for readability */
}
