/* ImageDetail.css */
.image-detail {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    z-index: 1000;
    
  }

  .title{
    margin-top: 100px;
    text-align: center;
    font-size: calc(10px + 2vmin);
    display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid; /* Cursor effect */
  animation: typing 5s steps(200, end), blink-caret 0.5s step-end infinite;
  }

  .description{
    text-align: center;
    font-size: calc(2vmin);
    display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid; /* Cursor effect */
  animation: typing 10s steps(200, end), blink-caret 0.5s step-end infinite;

  }

  @keyframes typing {
    from { width: 0; }
    to { width: 100%; }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent; }
    50% { border-color: black; }
  }

  .tilt-image-container {
    perspective: 1000px; /* Adds depth to the 3D effect */
  }
  
  .full-image {
    margin-top:100px;
    width: 60vw; /* Make the image responsive to the viewport width */
    object-fit: contain; /* Maintain aspect ratio */
    max-height: 70vh; /* Prevent the image from exceeding viewport height */
    max-height: 500px;
    transition: transform 0.1s ease-out; /* Smooth transition */
    transform-style: preserve-3d;
    
  }
  
  .back-button {
    position: absolute;
    top: 20px;
    left: 20px;
    padding: 10px 20px;
    font-size: calc(2vmin);
    cursor: pointer;
    color: #ffffff;
    background: transparent;
    border: white solid 1px;
    border-radius: 5px;
  }